<template>
    <home-container title="Profile" default-href="/home/account" :with-header='true'>
        <ion-label position='stacked'>Firstname</ion-label>
        <ion-item class='border ion-margin-bottom' lines='none'>
            <ion-input v-model='profile.firstname'></ion-input>
        </ion-item>
        <ion-label position='stacked'>Lastname</ion-label>
        <ion-item class='border ion-margin-bottom' lines='none'>
            <ion-input v-model='profile.lastname'></ion-input>
        </ion-item>
        <ion-label position='stacked'>Username</ion-label>
        <ion-item class='border ion-margin-bottom' lines='none'>
            <ion-input v-model='profile.username'></ion-input>
        </ion-item>
        <ion-label position='stacked'>Email</ion-label>
        <ion-item class='border ion-margin-bottom' lines='none'>
            <ion-input v-model='profile.email'></ion-input>
        </ion-item>
        <ion-label position='stacked'>Phone Number</ion-label>
        <ion-item class='border ion-margin-bottom' lines='none'>
            <ion-input v-model='profile.phone'></ion-input>
        </ion-item>
        
        <ion-button strong @click.prevent="openToast"  class='ion-margin-horizontal my-5' expand="block">
